import request from "@/libs/axios";
export function userLogin(data) {
  return request({
    url: "/account/login",
    method: "post",
    data: data
  });
}
export function resetPassword(data) {
  return request({
    url: "/account/resetPsw",
    method: "put",
    data: data
  });
}
export function getCurrent() {
  return request({
    url: "/account/current",
    method: "get"
  });
}
export function getCaptcha() {
  return request({
    url: "/account/captcha",
    method: "get"
  });
}